* {
  margin: 0;
  padding: 0;
  background-color: #031A30;
}

/* Works on Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #00EAFF none;
}

/* Works on Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 12px;
}

*::-webkit-scrollbar-track {
  background: inherit;
}

*::-webkit-scrollbar-thumb {
  background-color: #00EAFF;
  border-radius: 20px;
  border: 3px solid none;
}